import React from 'react';
import { IconButton } from '@mui/material';
import PropTypes from 'prop-types';
import DeleteIcon from '@mui/icons-material/Delete';
import { useDispatch } from 'react-redux';
import { StyledPriceBox } from '../stylesBuyPage';
import {
  StyledCardBox,
  StyledImgBox,
  StyledInfoBox,
  StyledInfoHeading,
  StyledInfoBrand,
  StyledPrice,
} from './stylesBagCard';
import { removeProductBag } from '../../../store/actions/actions';

const BagCardComponent = ({ product }) => {
  const dispatch = useDispatch();

  const removeCard = () => {
    dispatch(removeProductBag(product));
  };

  return (
    <StyledCardBox>
      <StyledImgBox>
        <img src={product.imageUrls[0]} alt='фото кроссовок найк' />
      </StyledImgBox>
      <StyledInfoBox>
        <StyledInfoHeading variant='h2'>{product.name}</StyledInfoHeading>
        <StyledInfoBrand varinat='p'>{product.categories}</StyledInfoBrand>
        <StyledInfoBrand varinat='p'>SIZE {product.size}</StyledInfoBrand>
        <IconButton
          onClick={removeCard}
          sx={{ position: 'absolute', bottom: 0, left: '0' }}
        >
          <DeleteIcon />
        </IconButton>
      </StyledInfoBox>
      <StyledPriceBox>
        <StyledPrice variant='p'>$ {product.currentPrice}</StyledPrice>
      </StyledPriceBox>
    </StyledCardBox>
  );
};

BagCardComponent.defaultProps = {
  product: {
    imageUrls: false,
    name: false,
    categories: false,
    size: false,
    currentPrice: false,
  },
};

BagCardComponent.propTypes = {
  product: PropTypes.shape({
    imageUrls: PropTypes.string,
    name: PropTypes.string,
    categories: PropTypes.string,
    size: PropTypes.string,
    currentPrice: PropTypes.string,
  }),
};

export default BagCardComponent;
