import React from 'react';
import PropTypes from 'prop-types';
import {
  StyledListItem,
  StyledLink,
  StyledLinkItem,
  StyledHeadingItem,
  StyledLinkText,
  StyledText,
  StyledLinkBox,
} from './stylesSliderComponent';

const SliderItemComponent = ({
  img,
  heading,
  linkText,
  price,
  idProduct,
  clickOnOneProduct,
}) => {
  return (
    <StyledListItem key={idProduct}>
      <StyledLink to={`/products/:${idProduct}`} onClick={clickOnOneProduct}>
        <img src={img} alt='фото кроссовок найк' />
        <StyledLinkBox>
          <StyledLinkItem>
            <StyledHeadingItem>{heading}</StyledHeadingItem>
            <StyledLinkText>{linkText}</StyledLinkText>
          </StyledLinkItem>
          <StyledText>{price}</StyledText>
        </StyledLinkBox>
      </StyledLink>
    </StyledListItem>
  );
};

SliderItemComponent.defaultProps = {
  img: false,
  heading: false,
  linkText: false,
  price: false,
  idProduct: false,
  clickOnOneProduct: false,
};

SliderItemComponent.propTypes = {
  img: PropTypes.string,
  heading: PropTypes.string,
  linkText: PropTypes.string,
  price: PropTypes.string,
  idProduct: PropTypes.string,
  clickOnOneProduct: PropTypes.string,
};

export default SliderItemComponent;
